<template>
  <div class="content" v-loading="loading" element-loading-text="系统加载中..." :fullscreen="true">

  </div>
</template>

<script setup>
/* eslint-disable */
import { onMounted, ref } from 'vue'
import { useRoute } from 'vue-router'
import { callback, nativeCallback } from '@/api/login'

const route = useRoute()

const loading = ref(false)
onMounted(async () => {
  loading.value = true
  let authPage = localStorage.getItem('authPage')
  if(route.query.type == 'qywxna'){
    console.log('qywxna')
    await nativeCallback(route.query)
  }else{
    console.log('default')
    await callback(route.query)
  }
  loading.value = false
  window.location.href = authPage
})
</script>

<style lang='scss' scoped>
.content{
  height: 100vh;
  width: 100vw;
}
</style>